/* Global CSS Variables */
:root {
  --color-primary: #F8E8E8;
  --color-secondary: #FDF2F2;
  --color-accent: #F5A9B8;
  --color-text: #5D4037;
  --color-text-secondary: #8D6E63;
  --color-border: #ECCCD2;
  --color-success: #A8E6CF;
  --color-error: #FF8A80;
  --color-error-bg: #FFEBEE;
  --color-error-border: #FFCDD2;
  --color-warning: #FFD3B6;
  --color-info: #A6D1E6;
  --color-background: #FEF9F9;
  --color-shadow: rgba(0, 0, 0, 0.1);
  --text-shadow: none;
  --border-radius: 8px;
  --transition-speed: 0.3s;
}

/* Base styles */
*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  transition: all var(--transition-speed) ease;
}

html, body, #root {
  height: 100%;
  width: 100%;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  background-color: var(--color-background) !important;
  color: var(--color-text) !important;
}

/* App container */
.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background-color: var(--color-background) !important;
  color: var(--color-text) !important;
}

/* Sidebar */
.sidebar {
  display: flex;
  flex-direction: column;
  width: 250px;
  height: 100%;
  background-color: var(--color-primary) !important;
  color: var(--color-text) !important;
  border-right: 1px solid var(--color-border) !important;
  z-index: 10;
  overflow-y: auto;
  padding-bottom: 60px; /* Space for footer */
}

.sidebar-header {
  padding: 1rem;
  border-bottom: 1px solid var(--color-border) !important;
  background-color: var(--color-primary) !important;
  color: var(--color-text) !important;
}

.sidebar-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0;
  color: var(--color-text) !important;
  text-shadow: var(--text-shadow) !important;
}

.sidebar-search {
  margin-top: 1rem;
  position: relative;
}

.sidebar-search input {
  width: 100%;
  padding: 0.5rem 0.75rem;
  border-radius: var(--border-radius);
  border: 1px solid var(--color-border);
  background-color: var(--color-secondary);
  color: var(--color-text);
}

.sidebar-search input:focus {
  outline: none;
  border-color: var(--color-accent);
}

.sidebar-search-icon {
  position: absolute;
  left: 0.5rem;
  top: 50%;
  transform: translateY(-50%);
  color: var(--color-text-secondary);
}

/* Sidebar sections */
.sidebar-section {
  margin-top: 1rem;
  padding: 0 1rem;
}

.section-title {
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: var(--color-text-secondary);
  margin-bottom: 0.5rem;
}

.favorites-section, .workspace-section {
  background-color: var(--color-primary) !important;
  color: var(--color-text) !important;
  border-color: var(--color-border) !important;
}

.favorites-title, .workspace-title {
  color: var(--color-text) !important;
  text-shadow: var(--text-shadow) !important;
}

/* Sidebar links */
.sidebar-link {
  display: flex;
  align-items: center;
  padding: 0.5rem 0.75rem;
  border-radius: var(--border-radius);
  margin-bottom: 0.25rem;
  text-decoration: none;
  color: var(--color-text) !important;
  background-color: var(--color-primary) !important;
}

.sidebar-link:hover {
  background-color: var(--color-secondary) !important;
}

.sidebar-link.active {
  background-color: var(--color-secondary) !important;
  color: var(--color-accent) !important;
  border-left: 3px solid var(--color-accent) !important;
}

.sidebar-link-icon {
  margin-right: 0.5rem;
}

/* Sidebar footer */
.sidebar-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 250px;
  padding: 1rem;
  background-color: var(--color-primary) !important;
  color: var(--color-text) !important;
  border-top: 1px solid var(--color-border) !important;
  z-index: 20;
}

.user-info {
  display: flex;
  align-items: center;
}

.user-avatar {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  margin-right: 0.5rem;
  background-color: var(--color-accent);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
}

.user-details {
  flex: 1;
}

.user-name {
  font-weight: 600;
  font-size: 0.875rem;
  color: var(--color-text);
}

.user-action {
  font-size: 0.75rem;
  color: var(--color-text-secondary);
  text-decoration: none;
}

/* Main content */
.content-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 60px); /* Adjust for header height if any */
  padding-bottom: 60px; /* Space for footer */
  position: relative;
}

.main-content {
  flex: 1;
  overflow-y: auto;
  padding: 1.5rem;
  position: relative;
  background-color: var(--color-background) !important;
  color: var(--color-text) !important;
  min-height: 100vh;
  padding-bottom: 60px; /* Space for footer */
}

.page-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: var(--color-text);
  text-shadow: var(--text-shadow) !important;
}

/* Cards */
.card {
  background-color: var(--color-primary);
  border-radius: var(--border-radius);
  box-shadow: 0 2px 4px var(--color-shadow);
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  border: 1px solid var(--color-border);
}

.card-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: var(--color-text);
  text-shadow: var(--text-shadow) !important;
}

/* Forms */
.form-group {
  margin-bottom: 1rem;
}

.form-label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: var(--color-text);
}

.form-control {
  width: 100%;
  padding: 0.5rem 0.75rem;
  border-radius: var(--border-radius);
  border: 1px solid var(--color-border);
  background-color: var(--color-secondary);
  color: var(--color-text);
}

.form-control:focus {
  outline: none;
  border-color: var(--color-accent);
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 1rem;
  border-radius: var(--border-radius);
  font-weight: 500;
  cursor: pointer;
  border: none;
  transition: all 0.2s ease;
}

.btn-primary {
  background-color: var(--color-accent);
  color: white;
}

.btn-primary:hover {
  background-color: var(--color-accent);
  opacity: 0.9;
}

.btn-secondary {
  background-color: var(--color-secondary);
  color: var(--color-text);
  border: 1px solid var(--color-border);
}

.btn-secondary:hover {
  background-color: var(--color-border);
}

.btn-danger {
  background-color: var(--color-error);
  color: white;
}

.btn-danger:hover {
  background-color: var(--color-error);
  opacity: 0.9;
}

.btn-icon {
  margin-right: 0.5rem;
}

/* Alerts */
.alert {
  padding: 1rem;
  border-radius: var(--border-radius);
  margin-bottom: 1rem;
}

.alert-success {
  background-color: var(--color-success);
  color: white;
}

.alert-error {
  background-color: var(--color-error-bg);
  color: var(--color-error);
  border: 1px solid var(--color-error-border);
}

.alert-warning {
  background-color: var(--color-warning);
  color: white;
}

.alert-info {
  background-color: var(--color-info);
  color: white;
}

/* Loading overlay */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.spinner {
  width: 2.5rem;
  height: 2.5rem;
  border: 0.25rem solid var(--color-border);
  border-top-color: var(--color-accent);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Force theme on ALL components */
body, html, #root, .app-container, .sidebar, .sidebar-header, .sidebar-footer, 
.favorites-section, .workspace-section, .sidebar-link, .sidebar-link.active {
  transition: all 0.3s ease !important;
}

/* Force sidebar to use theme colors */
.sidebar, 
.sidebar-header, 
.sidebar-footer, 
.favorites-section, 
.workspace-section {
  background-color: var(--color-primary) !important;
  color: var(--color-text) !important;
  border-color: var(--color-border) !important;
}

/* Force all sidebar links to use theme colors */
.sidebar a, 
.sidebar button,
.sidebar-link,
.favorites-item,
.workspace-item {
  color: var(--color-text) !important;
  background-color: var(--color-primary) !important;
}

.sidebar a:hover, 
.sidebar button:hover,
.sidebar-link:hover,
.favorites-item:hover,
.workspace-item:hover {
  background-color: var(--color-secondary) !important;
}

.sidebar a.active, 
.sidebar-link.active {
  background-color: var(--color-secondary) !important;
  color: var(--color-accent) !important;
  border-left: 3px solid var(--color-accent) !important;
}

/* Force all headers to use text shadow */
h1, h2, h3, h4, h5, h6, .sidebar-title, .favorites-title, .workspace-title {
  text-shadow: var(--text-shadow) !important;
}

/* Force the entire application to use theme colors */
.app-container, #root, body, html {
  background-color: var(--color-background) !important;
  color: var(--color-text) !important;
}

/* Force main content area to use theme background */
.main-content {
  background-color: var(--color-background) !important;
  color: var(--color-text) !important;
}

/* Footer styles have been updated to use fixed positioning */
.footer {
  display: none; /* Hide old footer style */
}

.copyright-footer {
  position: fixed;
  bottom: 0;
  left: 250px; /* Sidebar width */
  right: 0;
  text-align: center;
  padding: 1rem;
  background-color: var(--color-primary);
  color: var(--color-text);
  border-top: 1px solid var(--color-border);
  z-index: 100;
  font-size: 0.875rem;
}

.copyright-text {
  margin: 0;
  color: var(--color-text-secondary);
}

.tagline {
  display: block;
  margin-top: 0.25rem;
  font-style: italic;
  color: var(--color-text-secondary);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .sidebar {
    width: 100%;
    position: fixed;
    left: -100%;
  }
  
  .sidebar.open {
    left: 0;
  }
  
  .main-content {
    margin-left: 0;
  }
  
  .copyright-footer {
    left: 0;
  }
}

/* Notes page specific styles */
.notes-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1rem;
}

.note-card {
  background-color: var(--color-primary);
  border-radius: var(--border-radius);
  padding: 1rem;
  border: 1px solid var(--color-border);
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.note-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px var(--color-shadow);
}

.note-title {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: var(--color-text);
}

.note-content {
  color: var(--color-text-secondary);
  margin-bottom: 1rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  line-clamp: 3;
  -webkit-box-orient: vertical;
}

.note-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.75rem;
  color: var(--color-text-secondary);
}

.note-actions {
  display: flex;
  gap: 0.5rem;
}

.note-action {
  background: none;
  border: none;
  cursor: pointer;
  color: var(--color-text-secondary);
}

.note-action:hover {
  color: var(--color-accent);
}

.note-editor {
  height: calc(100vh - 200px);
  display: flex;
  flex-direction: column;
}

.note-editor-header {
  margin-bottom: 1rem;
}

.note-editor-title {
  width: 100%;
  font-size: 1.5rem;
  font-weight: 600;
  padding: 0.5rem;
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  background-color: var(--color-secondary);
  color: var(--color-text);
  margin-bottom: 1rem;
}

.note-editor-content {
  flex: 1;
  padding: 0.5rem;
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  background-color: var(--color-secondary);
  color: var(--color-text);
  resize: none;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.6;
}

.note-editor-content h1 {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: var(--color-text);
}

.note-editor-content h2 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
  color: var(--color-text);
}

.note-editor-content h3 {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: var(--color-text);
}

.note-editor-content p {
  margin-bottom: 1rem;
}

.note-editor-content ul, 
.note-editor-content ol {
  margin-bottom: 1rem;
  padding-left: 1.5rem;
}

.note-editor-content li {
  margin-bottom: 0.5rem;
}

.note-editor-content a {
  color: var(--color-accent);
  text-decoration: underline;
}

.note-editor-content img {
  max-width: 100%;
  height: auto;
  margin: 1rem 0;
  border-radius: var(--border-radius);
}

.note-editor-content blockquote {
  border-left: 3px solid var(--color-accent);
  padding-left: 1rem;
  margin-left: 0;
  margin-right: 0;
  font-style: italic;
  color: var(--color-text-secondary);
}

.note-editor-content code {
  font-family: monospace;
  background-color: var(--color-primary);
  padding: 0.2rem 0.4rem;
  border-radius: var(--border-radius);
  font-size: 0.9em;
}

.note-editor-content pre {
  background-color: var(--color-primary);
  padding: 1rem;
  border-radius: var(--border-radius);
  overflow-x: auto;
  margin-bottom: 1rem;
}

.note-editor-content pre code {
  background-color: transparent;
  padding: 0;
}

.note-editor-footer {
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
}

/* Rich text editor styles */
.toolbar-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  border-radius: var(--border-radius);
  background-color: var(--color-secondary);
  color: var(--color-text);
  border: 1px solid var(--color-border);
  cursor: pointer;
  transition: all 0.2s ease;
}

.toolbar-btn:hover {
  background-color: var(--color-accent);
  color: white;
}

.toolbar-btn:active {
  transform: translateY(1px);
}

.toolbar-divider {
  width: 1px;
  height: 1.5rem;
  background-color: var(--color-border);
  margin: 0 0.5rem;
}

.autosave-toggle {
  font-size: 0.8rem;
  color: var(--color-text-secondary);
}

/* Todo page specific styles */
.todo-container {
  max-width: 800px;
  margin: 0 auto;
}

.todo-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.todo-filters {
  display: flex;
  gap: 0.5rem;
}

.todo-filter {
  padding: 0.25rem 0.75rem;
  border-radius: var(--border-radius);
  background-color: var(--color-secondary);
  color: var(--color-text);
  border: 1px solid var(--color-border);
  cursor: pointer;
}

.todo-filter.active {
  background-color: var(--color-accent);
  color: white;
  border-color: var(--color-accent);
}

.todo-form {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.todo-input {
  flex: 1;
  padding: 0.5rem 0.75rem;
  border-radius: var(--border-radius);
  border: 1px solid var(--color-border);
  background-color: var(--color-secondary);
  color: var(--color-text);
}

.todo-list {
  list-style: none;
}

.todo-item {
  display: flex;
  align-items: center;
  padding: 0.75rem;
  border-radius: var(--border-radius);
  background-color: var(--color-primary);
  margin-bottom: 0.5rem;
  border: 1px solid var(--color-border);
}

.todo-item.completed {
  opacity: 0.7;
}

.todo-checkbox {
  margin-right: 0.75rem;
}

.todo-text {
  flex: 1;
  color: var(--color-text);
}

.todo-item.completed .todo-text {
  text-decoration: line-through;
  color: var(--color-text-secondary);
}

.todo-actions {
  display: flex;
  gap: 0.5rem;
}

.todo-action {
  background: none;
  border: none;
  cursor: pointer;
  color: var(--color-text-secondary);
}

.todo-action:hover {
  color: var(--color-accent);
}

.todo-action.delete:hover {
  color: var(--color-error);
}

.todo-empty {
  text-align: center;
  padding: 2rem;
  color: var(--color-text-secondary);
}

/* Fix for text direction */
body, input, textarea, button, select {
  direction: ltr !important;
}
